import React, { useState, useEffect } from 'react';
import { Form, Select, Input } from 'antd';
import axios from 'axios';

const { Option } = Select;

function Diachilienhe({ form }) {
    const [cities, setCities] = useState([]);
    const [districts, setDistricts] = useState([]);
    const [wards, setWards] = useState([]);
    const [selectedCity, setSelectedCity] = useState(null);
    const [selectedDistrict, setSelectedDistrict] = useState(null);

    useEffect(() => {
        fetchCities();
    }, []);

    const fetchCities = async () => {
        try {
            const response = await axios.get('https://raw.githubusercontent.com/sunrise1002/hanhchinhVN/refs/heads/master/dist/tree.json');
            if (response.data && typeof response.data === 'object') {
                setCities(Object.entries(response.data));
            } else {
                console.error('Invalid data format received from API');
                setCities([]);
            }
        } catch (error) {
            console.error('Error fetching cities:', error);
            setCities([]);
        }
    };

    const onCityChange = (value) => {
        const selectedProv = cities.find(([, city]) => city.name === value);
        if (selectedProv) {
            setSelectedCity(selectedProv[1]);
            setDistricts(Object.entries(selectedProv[1]['quan-huyen']));
            setWards([]);
            form.setFieldsValue({ district: undefined, ward: undefined });
        }
    };

    const onDistrictChange = (value) => {
        const selectedDist = districts.find(([, district]) => district.name === value);
        if (selectedDist) {
            setSelectedDistrict(selectedDist[1]);
            setWards(Object.entries(selectedDist[1]['xa-phuong']));
            form.setFieldsValue({ ward: undefined });
        }
    };

    return (
        <>
            <h2 className="apply-form__group-name">Địa chỉ liên hệ</h2>
            <div className="apply-form__group-body row no-gutters">
                <div className=" col-12 p-0">
                    <div className="MuiAutocomplete-root jss17 MuiAutocomplete-hasPopupIcon" aria-expanded="false">
                        <Form.Item name="City" label="Tỉnh/thành phố" rules={[{ required: true, message: 'Vui lòng chọn tỉnh/thành phố' }]}>
                            <Select onChange={onCityChange} size='large' placeholder="Chọn tỉnh/thành phố">
                                {cities.map(([, city]) => (
                                    <Option key={city.code} value={city.name}>{city.name}</Option>
                                ))}
                            </Select>
                        </Form.Item>
                    </div>
                </div>
                <div className=" col-12 col-lg-6 p-0 pr-12px pr-mb-0">
                    <div className=" col-12 p-0">
                        <div className="MuiAutocomplete-root jss17 MuiAutocomplete-hasPopupIcon" aria-expanded="false">
                            <Form.Item name="Quanhuyen" label="Quận/huyện" rules={[{ required: true, message: 'Vui lòng chọn quận/huyện' }]}>
                                <Select onChange={onDistrictChange} disabled={!selectedCity} size='large' placeholder="Chọn quận/huyện">
                                    {districts.map(([, district]) => (
                                        <Option key={district.code} value={district.name}>{district.name}</Option>
                                    ))}
                                </Select>
                            </Form.Item>
                        </div>
                    </div>
                </div>
                <div className=" col-12 col-lg-6 p-0 pr-12px pr-mb-0">
                    <div className=" col-12 p-0">
                        <div className="MuiAutocomplete-root jss17 MuiAutocomplete-hasPopupIcon" aria-expanded="false">
                            <Form.Item name="Phuongxa" label="Phường/xã" rules={[{ required: true, message: 'Vui lòng chọn phường/xã' }]}>
                                <Select disabled={!selectedDistrict} size='large' placeholder="Chọn phường/xã">
                                    {wards.map(([, ward]) => (
                                        <Option key={ward.code} value={ward.name}>{ward.name}</Option>
                                    ))}
                                </Select>
                            </Form.Item>
                        </div>
                    </div>
                </div>
                <div className=" col-12 p-0">
                    <div className="MuiAutocomplete-root jss17 MuiAutocomplete-hasPopupIcon" aria-expanded="false">
                        <Form.Item name="Diachi" label="Địa chỉ" rules={[{ required: true, message: 'Vui lòng nhập địa chỉ' }]}>
                            <Input size='large' placeholder='Nhập số nhà, tên đường...' />
                        </Form.Item>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Diachilienhe;